@charset "UTF-8";
.error_suces {
  padding: 0 !important;
  width: 100px !important; }
  .error_suces img {
    border-radius: 10px; }

.buttomActive {
  animation: dice-shadow 2s infinite; }

@keyframes dice-shadow {
  0% {
    box-shadow: 0 0 20px 8px #fff; }
  50% {
    box-shadow: 0 0 10px 2px #fff; }
  100% {
    box-shadow: 0 0 20px 8px #fff; } }

.active {
  cursor: pointer; }

img {
  max-width: 100%; }

.content {
  width: 100%;
  height: 100%;
  color: #fff; }

.container {
  margin-right: auto;
  margin-left: auto; }

/* 中等屏幕（桌面显示器，大于等于 768px） */
@media (min-width: 768px) {
  .container {
    width: 970px; } }

@media (min-width: 1260px) {
  .container {
    width: 1220px; } }

.money-box {
  margin: 30px auto;
  display: flex; }
  .money-box > div {
    width: 100%; }
  .money-box > div > div {
    display: inline-block;
    margin-left: 20px;
    margin-right: 20px;
    min-width: 130px;
    background: #fff;
    border: 1px solid #000;
    border-radius: 10px;
    justify-items: center;
    align-items: center;
    padding: 3px 10px;
    position: relative; }
    .money-box > div > div > img {
      width: 22px;
      vertical-align: text-bottom;
      margin-right: 5px;
      height: 20px; }
    .money-box > div > div > span {
      color: #000;
      font-weight: bold;
      font-size: 18px; }
    .money-box > div > div > div {
      cursor: pointer;
      position: absolute;
      min-width: 40px;
      height: 40px;
      border: 1px solid #000;
      border-radius: 50%;
      background: #fff;
      top: 50%;
      text-align: center;
      right: 0;
      transform: translate(50%, -50%); }
      .money-box > div > div > div > span {
        color: #fff;
        display: inline-block;
        width: 90%;
        line-height: 32px;
        vertical-align: text-top;
        height: 90%;
        background: #000;
        border-radius: 50%;
        font-weight: bold;
        font-size: 18px; }

.money-lang-en-box > div > div > span {
  color: #000;
  font-weight: bold;
  font-size: 18px; }

.money-lang-en-box > div > div > div {
  cursor: pointer;
  min-width: 90px;
  border-radius: 10px;
  right: -30px; }
  .money-lang-en-box > div > div > div > span {
    border-radius: 10px; }

.operation {
  background: #57936d;
  border: 0.5em solid transparent;
  background: linear-gradient(#57936d, #57936d) padding-box, repeating-linear-gradient(-45deg, #fff, #fff 12.5%, transparent 0, transparent 25%) 0/6em 6em;
  max-width: 700px;
  margin-right: 100px;
  border-radius: 10px;
  padding: 20px 60px; }
  .operation .dice_box {
    margin: 0 auto;
    position: relative;
    z-index: 9; }

.fc_box {
  margin-top: 20px;
  position: relative;
  z-index: 1; }

.fc-select {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 40px;
  flex-wrap: wrap; }

.fd-select-item.active {
  box-shadow: 0 0 20px 8px #bf3e05;
  border-radius: 20%; }

.logon_img {
  width: 22px;
  vertical-align: sub;
  height: 20px; }

.userOdds_box {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  font-weight: bold; }
  .userOdds_box li {
    width: 28%;
    background: #38623D;
    padding: 10px 20px;
    color: #fff;
    display: flex;
    flex-direction: column;
    font-size: 16px;
    border-radius: 10px;
    border: 1px solid #467B3C; }

.multiple {
  width: 100%;
  margin-top: 20px; }
  .multiple ul {
    display: flex;
    flex-direction: row;
    flex-flow: row wrap;
    justify-content: space-between; }
    .multiple ul > li {
      cursor: pointer;
      margin-top: 10px;
      display: flex;
      flex-direction: column; }
      .multiple ul > li > span {
        color: #fff;
        font-size: 16px;
        font-weight: 400; }
      .multiple ul > li > ul > li {
        display: flex;
        flex-direction: row; }
        .multiple ul > li > ul > li > div {
          border-radius: 3px;
          background: #38623D;
          padding: 5px;
          font-size: 16px;
          text-align: center;
          margin-right: 5px; }
          .multiple ul > li > ul > li > div:not(:first-child) {
            border: 1px solid #f9f775; }
          .multiple ul > li > ul > li > div:not(:first-child):hover {
            background: #f9f775;
            color: #416e46; }
        .multiple ul > li > ul > li > input {
          border-radius: 3px;
          width: 80px;
          border: 1px solid #f9f775;
          color: #fff;
          padding: 2px 5px;
          margin-right: 5px;
          font-size: 16px;
          background: #38623D; }

.btn_sumber {
  cursor: pointer;
  margin: 18px 5px;
  display: inline-block;
  width: 25%;
  padding: 10px;
  border-radius: 10px;
  background: #38623D;
  border: 1px solid #f9f775;
  justify-self: center;
  color: #fff;
  font-size: 16px;
  outline: none;
  transition: all 0.3s ease; }

.btn_sumber:hover {
  background: #f9f775;
  color: #416e46; }

.Ranking_box {
  background: #47613D;
  padding: 20px;
  min-height: 600px;
  border-radius: 10px;
  border: 0.5em solid transparent;
  background: linear-gradient(#47613D, #47613D) padding-box, repeating-linear-gradient(-45deg, #fff, #fff 12.5%, transparent 0, transparent 25%) 0/6em 6em; }
  .Ranking_box h2 {
    font-size: 18px;
    font-weight: bold;
    margin: 10px 0 5px;
    text-align: center; }
  .Ranking_box p {
    font-size: 14px;
    text-align: center;
    margin-bottom: 30px; }

/deep/.ivu-table td {
  background: #47613D; }

/deep/.ivu-table {
  color: #fff;
  background: #47613D; }

/deep/.ivu-table-wrapper {
  border: none; }

/deep/.ivu-table th {
  background: #47613D;
  border: none; }

/deep/.ivu-table:before {
  background-color: #47613D; }

/deep/.ivu-table:after {
  background-color: #47613D; }

/deep/.ivu-table td {
  border-bottom: 1px solid #47613D; }

.tabs_box {
  margin-top: 50px; }
  .tabs_box /deep/.ivu-table-row.ivu-table-row-hover td {
    background: #333333; }
  .tabs_box /deep/.ivu-table td {
    background: #416e46; }
  .tabs_box /deep/.ivu-table th {
    background: #416e46;
    border: none; }
  .tabs_box /deep/.ivu-table:before {
    background-color: #416e46; }
  .tabs_box /deep/.ivu-table:after {
    background-color: #416e46; }
  .tabs_box /deep/.ivu-table td {
    border-bottom: 1px solid #416e46; }

/deep/.ivu-tabs-nav-scroll {
  display: flex; }

/deep/.ivu-tabs .ivu-tabs-bar {
  border-width: 0; }

/deep/.nav-text.ivu-tabs-nav {
  margin: 0 auto; }
  /deep/.nav-text.ivu-tabs-nav .ivu-tabs-tab {
    font-weight: bold;
    color: #fff; }

/deep/.ivu-tabs-ink-bar {
  background-color: #fff; }

@media only screen and (max-width: 768px) {
  .fd-select-item {
    width: 40px; }
  .ivu-table-large {
    font-size: 13px; }
  .operation {
    margin: 20px !important;
    padding: 20px 10px; }
  .Ranking_box {
    padding: 10px 0;
    margin: 20px;
    min-height: auto; } }
